import { defineComponent } from "@vue/composition-api";
import axios, { AxiosResponse } from "axios";

export default defineComponent({
	name: "Pagination",
	computed: {
		hasNext() {
			return this.$store.state.pagination.next;
		},
		hasPrev() {
			return this.$store.state.pagination.prev;
		},
		currentPage() {
			return parseInt(this.$route.query.page as string);
		},
		pagesCount() {
			return this.$store.state.pagination.pages;
		},
	},
	methods: {
		getPages(c: number, m: number) {
			const current = c;
			const last = m;
			const delta = 2;
			const left = current - delta;
			const right = current + delta + 1;
			const range = [];
			const rangeWithDots = [];
			let l;

			for (let i = 1; i <= last; i++) {
				if (i == 1 || i == last || (i >= left && i < right)) range.push(i);
			}
			for (const i of range) {
				if (l) {
					if (i - l === 2) rangeWithDots.push(l + 1);
					else if (i - l !== 1) rangeWithDots.push("...");
				}
				rangeWithDots.push(i);
				l = i;
			}

			return rangeWithDots;
		},
		async paginate(action: "prev" | "next") {
			const pagination = this.$store.state.pagination;
			const query = this.$route.query;
			let page: number;

			if (!pagination[action]) return;
			if (!query.page) query.page = "1";
			if (action === "next") {
				page = parseInt(query.page as string) + 1;
			} else {
				page = parseInt(query.page as string) - 1;
			}

			this.$store.commit("setLoadingCharacters", true);
			await axios.get(pagination[action]).then((response: AxiosResponse) => {
				const data = response.data;

				this.$store.commit("setCharacters", data);
				this.$store.commit("setPage", page);
				this.$router.push({ path: "characters", query: { page: page + "" } });
				this.$store.commit("setLoadingCharacters", false);
			});
		},
		async goTo(page: number) {
			const url = `https://rickandmortyapi.com/api/character/?page=${page}`;

			this.$store.commit("setLoadingCharacters", true);
			await axios.get(url).then((response: AxiosResponse) => {
				const data = response.data;

				this.$store.commit("setCharacters", data);
				this.$store.commit("paginate", page);
				this.$router.push({ path: "characters", query: { page: page + "" } });
				this.$store.commit("setLoadingCharacters", false);
			});
		},
	},
});
