import { defineComponent } from "@vue/composition-api";
import axios, { AxiosResponse } from "axios";

import Pagination from "@/components/Pagination/Pagination.vue";
import Loader from "@/components/Loader/Loader.vue";

export default defineComponent({
	name: "Characters",
	components: {
		Pagination,
		Loader,
	},
	data() {
		return {
			q: "",
		};
	},
	mounted() {
		this.setPage();
		this.getCharacters();
	},
	computed: {
		characters() {
			return this.$store.getters.characters;
		},
		filterCharacters() {
			return this.$store.getters.filterCharacters;
		},
		loading() {
			return this.$store.state.loading.characters;
		},
	},
	methods: {
		setPage() {
			const queryPage = this.$route.query.page;
			const page: number = this.$store.state.pagination.currentPage || parseInt(queryPage as string) || 1;

			if (!queryPage) {
				this.$router.push({
					path: "characters",
					query: { page: page + "" },
				});
			}
			this.$store.commit("setPage", page);
		},
		async getCharacters() {
			const page: number = this.$store.state.pagination.currentPage;
			const url = `https://rickandmortyapi.com/api/character/?page=${page}`;

			await axios.get(url).then((response: AxiosResponse) => {
				const data = response.data;

				this.$store.commit("setCharacters", data);
				this.$store.commit("setLoading", { loadingKey: "characters", value: false });
			});
		},
		filterCharacterByName(event: { target: { value: string } }) {
			this.q = event.target.value;

			this.$store.commit("filterCharacterByName", this.q);
		},
		goToCharacterDetail(id: number) {
			this.$router.push(`/characters/${id}`);
		},
	},
});
